<template>
 <div>
    <h3>1111111111</h3>
    <ul class="mui-table-view">
        <!-- <li class="mui-table-view-cell mui-media">
            <a href="javascript:;">
                <img class="mui-media-object mui-pull-left" src="https://ss2.bdstatic.com/70cFvnSh_Q1YnxGkpoWK1HF6hhy/it/u=3143697040,2811728168&fm=27&gp=0.jpg">
                <div class="mui-media-body">
                    <h1>幸福</h1>
                    <p class='mui-ellipsis'>
                        <span>发表时间 : 2012-12-12 12:12:12</span>
                         哈喽
                        <span>点击：0次</span>
                       
                    </p>
                </div>
            </a>
        </li> -->
         <li class="mui-table-view-cell mui-media" v-for="item in this.newListData" :key="item.id">
            <router-link :to="'/home/newsInfo/' + item.id">
                <img class="mui-media-object mui-pull-left" :src="item.img" alt=""> -->
                <div class="mui-media-body">
                     <!-- <h1>{{ item.img}}</h1> -->
                    <h1>{{ item.title}}</h1>
                    <p class='mui-ellipsis'>
                        <span>发表时间 : {{item.add_time | dateFormat("YYYY-MM-DD HH:mm")}}</span>
                         <!-- 哈喽 -->
                        <span>点击: {{item.click}} 次</span>
                       
                    </p>
                </div>
            </router-link>
        </li>
    </ul>
 </div>   
</template>

<script>
import { Toast } from "mint-ui";
import { log } from 'util';
export default {
    data()
    {
        return{
                newListData: []
               
              
        };
    }
    ,
    created(){
        this.getNewsListData();
    }
    ,
    methods:{
        getNewsListData(){
            this.$http.get("newListData.json?"+  Math.random()).then(result => {
                //  result.setContentType("text/html;charset=UTF-8");  
                  if(result.body.status === 0){
                    console.log("获取新闻列表数据成功 " + result.body.status);
                    this.newListData = result.body.message;
                    Toast('获取新闻列表数据成功');
                      console.log( result.body.message)
                   // console.log(this.newListData[0].title)
                  }
                  else{
                    console.warn("获取新闻列表数据失败");
                    Toast('获取新闻列表数据失败');
                }
            }
            // ,error => {
            //     console.log('错误了！！错误了！！错误了！！错误了！！错误了！！') 
            // }
            )
        }
    }
    
}
</script>

<style lang="scss" scoped>
    .mui-table-view{
        li{
            h1{font-size: 14px;}
            .mui-ellipsis{
                font-size: 12px;
                color:turquoise;
                display: flex;
                justify-content: space-between;
            }
        }
    }
    
</style>




