<template>
    <div class="cmt-container">
        <h3>评论子组件</h3>
        <hr>
        <textarea placeholder="请输入要BB的内容(最多吐槽120字)"
            maxlength="120" v-model="msg"></textarea>
        <mt-button type="primary" size="large" @click="postComment">发表评论</mt-button>

        <div class="cmt-list">
            <div class="cmt-item" v-for="(item, i) in commentInfo" :key="i">
                <div class="cmt-title">
                    <!-- 第{{item.id}} {{i + 1}}楼&nbsp;&nbsp;用户：{{item.user_name}}&nbsp;&nbsp;发表时间：{{item.add_time | dateFormat("YYYY-MM-DD HH:mm")}} -->
                    第{{i + 1}}楼&nbsp;&nbsp;用户：{{item.user_name}}&nbsp;&nbsp;发表时间：{{item.add_time | dateFormat("YYYY-MM-DD HH:mm")}}
                </div>
                <div class="cmt-body">
                    {{ item.content === "undefined" ? "此用户很懒，没有做出评" : item.content}}
                </div>
            </div>
            <!-- 多余的 -->
             <!-- <div class="cmt-item">
                <div class="cmt-title">
                    第一楼&nbsp;&nbsp;用户：匿名用户&nbsp;&nbsp;发表时间：2012-12-12 12:12:12
                </div>
                <div class="cmt-body">
                    锄禾日当午，汗滴禾下土，谁知盘中餐，粒粒皆辛苦
                </div>
            </div>

             <div class="cmt-item">
                <div class="cmt-title">
                    第一楼&nbsp;&nbsp;用户：匿名用户&nbsp;&nbsp;发表时间：2012-12-12 12:12:12
                </div>
                <div class="cmt-body">
                    锄禾日当午，汗滴禾下土，谁知盘中餐，粒粒皆辛苦
                </div>
            </div> -->
            <!-- 多余的 -->
        </div>

        <mt-button type="danger" size="large" plain @click="this.getMoreCommentInfoData">加载更多</mt-button>
    </div>
</template>

<script>
import{ Toast } from "mint-ui";
export default {
    data() {
        return {
            pageIndex: 1 ,//默认展示第一页数据
            commentInfo: [],
            msg:''
        }
    },
     created(){
        this.getCommentInfoData();
    },
    methods: {
         getCommentInfoData(){
             this.$http.get("commentData_" + this.pageIndex + ".json?"+  Math.random()).then(result => {
                //  result.setContentType("text/html;charset=UTF-8");  
                  if(result.body.status === 0){
                    // this.commentInfo = result.body.message;
                    this.commentInfo = this.commentInfo.concat(result.body.message);
                    Toast('获取评论数据成功');
                    console.log( result.body.message)
                  }
                  else{
                    console.warn("获取评论数据失败");
                    Toast('获取评论数据失败');
                }
             })
         },
         getMoreCommentInfoData(){
             this.pageIndex++;
             this.getCommentInfoData();
         },
         postComment()
         {  
             let _msg = this.msg.trim();
             if(_msg.length === 0){
                 Toast('评论内容不能为空');
                 return;
             }
             //因为没服务器 临时处理评论

            var cmt = {
                        user_name: "大胡子用户",
                        add_time: Date.now(),
                        content: _msg
                    };
             this.commentInfo.unshift(cmt);
             this.msg = '';
             return;

             // 发表评论  目前没有服务器
             // 路由的ID = $route.params.id;
             // 参数1： 请求的URL地址
             // 参数2： 提交各服务器的数据对象（content: this.msg）
             // 参数3： 定义提交时候,表单中数据格式 ( emulateJSON:true ) 注：已在全局设置了默认的
             this.$http
             .post('api/postcomment/' + this.$route.params.id,
                { content:_msg })
             .then(function(result) {
                 if(result.body.status === 0){
                     //1.拼接出一个评论对象
                     var cmt = {
                         user_name: "大胡子用户",
                         add_time: Date.now(),
                         content: _msg
                     };
                     this.commentInfo.unshift(cmt);
                     this.msg = '';
                 }
             });
         }
     },
     props: ['id']
}
</script>


<style lang="scss" scoped>
    .cmt-container{
        h3{
            font-size: 18px;
        }
        textarea{
            font-size: 14px;
            height: 85px;
            margin: 0;
        }

        .cmt-list{
            margin: 5px 0;
            .cmt-item
            {
                font-size: 13px;
                .cmt-title
                {
                    line-height: 30px;
                    background-color: #ccc;
                }
                .cmt-body
                {
                    line-height: 35px;
                    text-indent: 2em;
                }
            }
        }
    }
</style>



