<template>
    <div class="newsInfo-container">
            <!-- <h3>新闻详情 ---- {{ id }}</h3>
            <h3>新闻详情 ---- {{ $route.params }}</h3> -->
            <h3 class="title">{{ newsInfo.title }}</h3>
        <p class="subtitle">
            <span>发表时间：{{ newsInfo.add_time | dateFormat("YYYY-MM-DD HH:mm")}}</span>
            <span>点击：{{ newsInfo.click }}次</span>
        </p>

        <hr>

        <div class="content" v-html="newsInfo.content"></div>
        <div class="content" v-html="cat1"></div>
        <!-- <div class="content" :url="newsInfo.content"></div> -->
        <comment-box  :id="this.id"></comment-box>

    </div>
</template>

<script>
import comment from "../subComponents/Comment.vue"

import { Toast } from "mint-ui";
export default {
    data(){
        return {
            id: this.$route.params.id,

            newsInfo:{}, //新闻信息对象

           // cat:"<img src='//www.w3cplus.com/sites/default/files/blogs/2017/1709/cat.jpg' />"
           cat:"<html><img src='//www.w3cplus.com/sites/default/files/blogs/2017/1709/cat.jpg' /> </html>"
           ,
           cat1:"<html><body><p>来自另一个文件夹的图像：<img src='/i/ct_netscape.jpg'/></p><p>来自 W3School.com.cn 的图像：<img src='http://www.w3school.com.cn/i/w3school_logo_white.gif' /></p></body></html>"
        };
    },
    created(){
        this.getNewsInfoData();
    },
    methods:{
        getNewsInfoData(){//获取新闻详细
            this.$http.get("newInfoData.txt?"+  Math.random()).then(result =>{
                if(result.body.status === 0){
                    for (var i=0; i < result.body.message.length;i++)
                    {
                        if(this.id == result.body.message[i].id)
                        {
                            this.newsInfo = result.body.message[i];
                            return;
                        } 
                    }
                    this.newsInfo = result.body.message[0];
                    
                    //Toast("获取新闻详细内容成功！")
                }else{
                    Toast("获取新闻详细内容失败！")
                }
        })}
    }
    ,
    components:{
        "comment-box" : comment
    }
}
</script>


<!-- <style lang="scss" scoped>    为了照顾 .content   img 的样式（为什么去掉呢    
                                （ 我估计 v-html  里面的 img 没有 scoped 局部样式，不去掉改变不了） 
                                  瞎猜的~~  ）-->
<style lang="scss">
.newsInfo-container{
    padding: 0 4px;
    .title{
        font-size: 16px;  
        text-align: center;
        margin: 15px 0;
        color: red;
    }
    .subtitle{
        font-size: 13px;
        color: #226aff;
        // 对齐
        display: flex;
        justify-content: space-between;

    }
    .content{
        img{
            width: 100%;
        }
    }
}
</style>


