<template>
    <div>
        
        <!-- 轮播图区域 -->
        <mt-swipe :auto="5000">
            <!-- <mt-swipe-item>1</mt-swipe-item>
            <mt-swipe-item>2</mt-swipe-item>
            <mt-swipe-item>3</mt-swipe-item> -->

            <mt-swipe-item v-for="item in lunbotuList" :key="item.id">
                 <!-- <p style="color:#000000">{{  lunbotuList }}</p>
                  <p style="color:#000000"> ============================== =======================</p>
                 <p style="color:#000000">{{  item.id }}</p> -->
                <img :src="item.img" alt="">
            </mt-swipe-item>
        </mt-swipe>

        <!-- grid 6宫格 -->
        <ul class="mui-table-view mui-grid-view mui-grid-9">
            <li class="mui-table-view-cell mui-media mui-col-xs-4 mui-col-sm-3">
                <router-link to="/home/newsList">
                    <!-- <span class="mui-icon mui-icon-home"></span> -->
                    <img src="@/assets/icons/11.png" alt="">
                    <div class="mui-media-body">新闻资讯</div></router-link></li>
            <li class="mui-table-view-cell mui-media mui-col-xs-4 mui-col-sm-3">
                <router-link to="/home/photoList">
                    <img src="@/assets/icons/12.png" alt="">
                    <div class="mui-media-body">图片分享</div>
                </router-link></li>
            <li class="mui-table-view-cell mui-media mui-col-xs-4 mui-col-sm-3"><a href="#">
                    <!-- <span class="mui-icon mui-icon-chatbubble"></span> -->
                    <img src="@/assets/icons/10.png" alt="">
                    <div class="mui-media-body">商品购买</div></a></li>
            <li class="mui-table-view-cell mui-media mui-col-xs-4 mui-col-sm-3"><a href="#">
                    <span class="mui-icon mui-icon-location"><span class="mui-badge">5</span></span>
                    <div class="mui-media-body">留言反馈</div></a></li>
            <li class="mui-table-view-cell mui-media mui-col-xs-4 mui-col-sm-3"><a href="#">
                    <span class="mui-icon mui-icon-search"></span>
                    <div class="mui-media-body">视频专区</div></a></li>
            <li class="mui-table-view-cell mui-media mui-col-xs-4 mui-col-sm-3"><a href="#">
                    <span class="mui-icon mui-icon-phone"></span>
                    <div class="mui-media-body">联系我们</div></a></li>
        
        </ul> 
        <!-- 标题 -->
        <h3> HomeContainer </h3>
    </div>
</template>


<script>

import {Toast} from 'mint-ui'
import { log } from 'util';

export default {
    // data:function () {
    //     return{}
    // }
    // ,
    data() {
        return{
            lunbotuList: [
                // {id:"第一组"},
                // {id:"第二组"},
                // {id:"第三组"}
            ]// 保存轮播图数组
        };
    }
    ,
    created()
    {
        this.getLunbotu();
    }
    ,
    methods: {
        getLunbotu(){ //获取轮播图数据的方法
            // this.$http.get("http://www.liulongbin.top:3005/api/getprodlist").then(result => {
            //     console.log(result.body);
            // })
            
             this.$http.get("data.txt?" +  Math.random()).then(result => {
                console.log(result.body);
                if(result.body.status === 0){
                    this.lunbotuList = result.body.message;
                    console.log("ddddd: "  + this.lunbotuList)
                     //Toast('获取轮播图数据成功');
                    // Toast({
                    //     message: '获取轮播图数据成功',
                    //     iconClass: 'icon icon-success'
                    // });
                    Toast({
                        message: '获取轮播图数据成功',
                         iconClass: 'icon icon-success',
                         //iconClass: 'fa fa-check fa-5x',
                        duration: 1000
                    });
                }
                else{
                    console.warn("获取轮播图数据失败");
                    Toast('获取轮播图数据失败');
                }
            })
        }
    }

}
</script>


<style lang="scss" scoped>
// .mt-swipe{
//      height: 200px;
// }
 .mint-swipe{
     height: 200px;

    //  .mint-swipe-item:nth-child(1){
    //  background-color :rebeccapurple;
    // }
    //  .mint-swipe-item:nth-child(2){
    //  background-color: red;
    // }
    //  .mint-swipe-item:nth-child(2){
    //  background-color: yellow;
    // }

     .mint-swipe-item{
         &:nth-child(1){
             background-color: red;
         }
          &:nth-child(2){
             background-color: blue;
         }
          &:nth-child(3){
             background-color: cyan;
         }

         img{
             width: 100%;
             height: 100%;
         }
         .mui-media-body{
             font-size: 13px;
         }
     }
 }

//  .ul{
//      background-color: white;
//  }

.mui-grid-view.mui-grid-9{
    background-color:white;
    border:none;
    img{
        width: 60px;
        height: 60px;
    }
}
.mui-grid-view.mui-grid-9 .mui-table-view-cell
{
    border:none;
}

 
</style>






