// 把路由对象暴露出去  相当于： （var router = new Router）， export default  router   
export default new Router({
 // mode:'history',  //使用history防止url中出现#
  mode:'hash',    //使用hash url中出现#
  routes: [
    {
      path: '/', redirect: '/home'
    }
    ,{
      path: '/home',
      name: 'HomeContainer',
      component: HomeContainer
    }
    ,{
      path: '/member',
      name: 'MemberContainer',
      component: MemberContainer
    }
    ,{
      path: '/shopcar',
      name: 'ShopcarContainer',
      component: ShopcarContainer
    }
    ,{
      path: '/search',
      name: 'SearchContainer',
      component: SearchContainer
    }
    ////////////////////
    ,{
      path: '/home/newsList',
      name: 'NewsListContainer',
      component: NewsList
    }

    ,{
      path: '/home/newsInfo/:id',
      name: 'NewsInfoContainer',
      component: NewsInfo
    }

    ////////////////////////
    ,{
      path: '/home/photoList',
      name: 'PhotoList',
      component: PhotoList
    }

   

    // ,{
    //   path: '*',
    //   name: 'notFind',
    //   component: notFind
    // }
    // // ,{path:'*', redirect: '/hello1'}//（输入错误重新定向）设置默认指向

  ],
  linkActiveClass: 'mui-active' // 覆盖默认的路由高亮的类，默认的类叫做 router-link-active (当点击路由系统会默认使用  系统自带)
   
   
})



